import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../http/product-manage.http.service";
import { ConfirmationService } from 'primeng/primeng';//
import { ClassificationApplicationTypeBean } from './bean/classification-application-type.bean';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
  selector: 'app-classification-application-type',
  templateUrl: './classification-application-type.component.html',
  styleUrls: ['./classification-application-type.component.scss'],
  providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
/**
 * -
 */
export class ClassificationApplicationTypeComponent implements OnInit {
  tabMesg: any = tableMessage;//
  // bean
  classificationApplicationTypeBean: ClassificationApplicationTypeBean = new ClassificationApplicationTypeBean();
  //
  updateValue: any = [];
  //
  msgs: Message[] = [];
  //
  total: string;
  first: number = 0;//
  //
  showModel: string;
  display: boolean = false;
  //
  headerTitle: string;
  // 
  site_list: any = [];//

  constructor(public productManageHttpService: ProductManageHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc) { }

  ngOnInit() {
    this.queryClick();
  }

  // 
  resetClick() {
    this.classificationApplicationTypeBean = new ClassificationApplicationTypeBean()
  }

  //
  querySecle() {
    this.classificationApplicationTypeBean.pageSize = 10;
    this.classificationApplicationTypeBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }
  //  
  queryClick() {
    this.productManageHttpService.queryProdCatalogCategoryTypeList(this.classificationApplicationTypeBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.site_list = data.resultList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
  //
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        //
        let param = {
          prodCatalogCategoryTypeId: item.prodCatalogCategoryTypeId,
        };
        this.productManageHttpService.delProdCatalogCategoryType(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
            this.first = 0;
            this.queryClick();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
      }
    });
  }
  //
  addClick() {
    this.display = true;
    this.headerTitle = '';
    this.showModel = '1';
  }
  //
  addCall(param) {
    if (param.code == 'cancelModel') {
      this.display = param.display;
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '', detail: param.code });
      this.display = param.display;
    }
    this.first = 0;
    this.queryClick();
  }
  // 
  modifyClick(item) {
    this.display = true;
    this.headerTitle = '';
    this.showModel = '2';
    this.updateValue = item;
  }
  //
  updateCall(param) {
    if (param.code == 'cancelModel') {
      this.display = param.display;
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '', detail: param.code });
      this.display = param.display;
    }
    this.first = 0;
    this.queryClick();
  }
  //
  paginate(event) {
    // ;
    //
    this.classificationApplicationTypeBean.pageSize = event.rows * 1;
    //
    let currentPage = event.page + 1;
    this.classificationApplicationTypeBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.classificationApplicationTypeBean.pageSize;
    //
    this.queryClick();
    this.classificationApplicationTypeBean.pageNum = 1;
  }
  //
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
